
import React, { PureComponent } from 'react'

export default class _app extends PureComponent {


  render() {
    return (
      <div id='main' className='body'>
        <div className='mainBox'>
        <div className='roxBox'>
            <div className='lu'></div>
            <div></div>
            <div className='ru'></div>
        </div>
        <div className='roxBox'>
            <div></div>
            <div className='m'></div>
            <div></div>
        </div>
        <div className='roxBox'>
            <div className='ll'></div>
            <div></div>
            <div className='rl'></div>
        </div>
        </div>

        <style jsx>
          {`
              .body {
                background-color: black;
                width: 100vh;
                height: 100vh;
                display: flex;
                justify-content: center;
                align-items: center;
              }
              .mainBox {
                height: 600px;
                width: 600px;
                }
              .lu{
                border-color: white;
                border-style: solid;
                border-width: 0px 1px 1px 0px;
              }
              .ru{
                border-color: white;
                border-style: solid;
                border-width: 0px 0px 1px 1px;
              }
              .m{
                border-color: white;
                border-style: solid;
                border-width: 1px;
              }
              .ll{
                border-color: white;
                border-style: solid;
                border-width: 1px 1px 0px 0px;
              }
              .rl{
                border-color: white;
                border-style: solid;
                border-width: 1px 0px 0px 1px;
              }
              .rowBox{
                height: 33.33%;
                display: flex;
              }
              .sepBox{
                width: 33.33%;
              }
          `}
        </style>
      </div>
    )
  }
}
